@import url('https://fonts.googleapis.com/css?family=Raleway:400,500,600');
@import 'common/settings';

$s-xxs:		380px;
$s-md:		768px;

@mixin df() {
  display: -webkit-flex;
  display: flex;
}

@mixin df-lc() {
  -webkit-justify-content: flex-start;
  justify-content: flex-start;
  -webkit-align-items: center;
  align-items: center;
}

@mixin df-lt() {
  -webkit-justify-content: flex-start;
  justify-content: flex-start;
  -webkit-align-items: flex-start;
  align-items: flex-start;
}

@mixin df-rc() {
  -webkit-justify-content: flex-end;
  justify-content: flex-end;
  -webkit-align-items: center;
  align-items: center;
}

@mixin df-lb() {
  -webkit-justify-content: flex-start;
  justify-content: flex-start;
  -webkit-align-items: flex-end;
  align-items: flex-end;
}

@mixin df-cb() {
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-align-items: flex-end;
  align-items: flex-end;
}

@mixin df-col() {
  -webkit-flex-direction: column;
  flex-direction: column;
}

@mixin df-row() {
  -webkit-flex-direction: row;
  flex-direction: row;
}

@mixin df-f1() {
  -webkit-flex: 1 !important;
  flex: 1 0;
}

@mixin df-c() {
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-align-items: center;
  align-items: center;
}

@mixin transform($transforms) {
	-webkit-transform: $transforms;
	   		transform: $transforms;
}

@mixin transition($transition...) {
  -webkit-transition: $transition;
  transition:         $transition;
}

html {  
  line-height: normal;
}

.qh-root {
  font-family: 'Raleway';
  font-size:16px;
  background: transparent;
  border: none;
  bottom: 30px;
  
  position: fixed;
  right: 30px;
  top: auto;
  
  z-index: 1002;
  
  .widget_base {
    cursor: pointer;
    transition: opacity 0.2s linear;
    width: 60px;
    height: 60px;
  }

  .widget-outlook__background-circle,
  .outlook--background {
    width: 100%;
    height: 100%;
  }

  .widget_base:hover {
    opacity: 0.75;
  }

  button, input, select, textarea {
    font-family: inherit;
    line-height: inherit;
  }
  
  * {
    box-sizing: border-box;
  }
  
  p {
    margin: 0;
    font-family: Raleway;
  }

  svg,
  img {
    vertical-align: middle;
  }

  img {
    border: 0;
  }
  
  button,
  button:focus {
    outline: none;
    box-shadow: none;
  }

  .f1 {
    @include df;
    @include df-f1;
  }

  .fc {
    @include df;
    @include df-c;
  }

  .fct {
    -webkit-justify-content: flex-start;
    justify-content: flex-start;
    -webkit-align-items: center;
    align-items: center;
  }

  .flc {
    @include df;
    @include df-lc;
  }

  .flt {
    @include df;
    @include df-lt;
  }

  .frc {
    @include df;
    @include df-rc;
  }

  .fcol {
    @include df;
    @include df-col;
  }

  .fcb {
    @include df;
    @include df-cb;
  }

  .messages,
  .errornote {
    width: 100%;
    padding: 0;
  }

  .messages {
    margin: 0;
    margin-bottom: 10px;
  }

  .errornote {
    color: white;
    font-size: 0.75em;
  }

  .errorlist {
    margin: 0;
    padding: 0;
  }

  .messages li,
  .errorlist li {
    list-style: none;
  }

  .messages li.error {
    color: #c90000;
  }

  .messages li.success {
    color: #00a100;
  }

  div.loader,
  div.loader:after {
    border-radius: 50%;
    width: 48px;
    height: 48px
  }

  div.loader {
    z-index: 1;
    margin: auto;
    font-size: .25rem;
    position: fixed;
    top: calc(50% - 24px);
    left: calc(50% - 24px);
    text-indent: -9999em;
    border-right: .3em solid #80d100;
    border-bottom: .3em solid #001588;
    border-top: .3em solid #001588;
    border-left: .3em solid #80d100;
    transform: translateZ(0);
    animation: load8 1.1s infinite linear
  }

  @-webkit-keyframes load8 {
    0% {
      transform: rotate(0deg)
    }
    to {
      transform: rotate(1turn)
    }
  }

  @keyframes load8 {
    0% {
      transform: rotate(0deg)
    }
    to {
      transform: rotate(1turn)
    }
  }

  .wrapper--thin {
    width: 600px;
    margin-top: 78px;
  }

  .widget-templates {
    margin-bottom: 50px;
  }

  .outlook {
    position: relative;
    min-height: 100px;
    background-color: $color--white-smoke;
    border: 1px solid $color--light-grey;
    padding: $spacing-outer;
  }

  .widget-outlook__graphics {
    position: relative;
    box-shadow: 0px 0px 10px 3px lightgrey;
    border-radius: 50%;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }

  .qh_widget-bottom__msg--switchable {
    display: none;
  }

  .qh_widget-bottom__msg--switchable.js-active {
    display: flex;
  }

  .widget-outlook__alert {
    display: none;
    position: absolute;
    top: -8px;
    left: -8px;
    z-index: 1;
  }

  .widget-outlook__alert.js-active {
    display: block;
  }

  .widget-outlook__icon_wrap {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: 50%;
    padding: 3px;
  }

  .widget-outlook__icon {
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 1;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    overflow: hidden;
    border-radius: 50%;
  }

  .widget-outlook__icon svg {
    display: block;
    height: 40px;
    max-width: 80px;
  }

  .js-hidden {
    visibility: hidden;
  }

  // EXT

  .qh-widget-ext__messages-content {
    height:100%
  }

  .widget-extension__complete-icon {
    transform: translate(-9px,3px);
  }

  .widget-extension__messages li {
    color: white;
    font-size: 0.875em;
    font-weight: 600;
    text-align:center;
  }

  .widget-extension__messages li:nth-child(1) {
    font-size:16px;
    font-size:500;
  }

  .widget-extension__messages li:nth-child(2) {
    font-size:400;
  }

  .widget-extension__messages li.error {
    color: #c90000;
  }

  .widget-extension__messages li.success {
    color: white;
  }

  .qh-widget-ext__content p {
    color: white;
    font-size: 0.875em;
  }

  .qh-widget-ext__content a {
    color: $color--apricot;
  }

  .qh_widget-closer {
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
    z-index: 1;
  }

  .qh_widget-closer:hover {
    opacity: 0.7;
  }

  .qh_widget-closer--active-operator svg {
    width:36px;
    height:36px;
  }

  .qh_widget-closer--inactive-operator svg {
    width:24px;
    height:24px;
  }

  .widget-extension__form-group {
    margin-bottom: 2px;
    position: relative;
  }

  .widget-extension__errornote {
    position: absolute;
    top: -14px;
    left: 2px;
  }
  .widget-extension__errornote li {
    font-weight:500;
  }

  .widget-extension__form-text {
    font-weight: 600;
    color: white;
    font-size: 0.875em;
  }

  .widget-extension__label {
    margin-bottom: 2px;
  }

  input,
  textarea {
    font-weight: 500;
    font-size: 0.825em;
    padding:10px;
    color: $color--nero;
    border:2px solid white;
    border-radius: 6px;
    margin-bottom:14px;
    box-shadow: 0 0 0 3px hsla(0,0%,0%,.15);
  }

  textarea {
    height: 70px;
    resize: none;
    width: 100%;
  }

  input.error,
  textarea.error {
    border-color:#FC1A19;
  }

  .widget-extension__button {
    cursor: pointer;
    background-color: #1b1b1b;
    padding: 8px 14px 8px 24px;
    border-radius: 50px;
    box-shadow: 0 0 0 4px hsla(0,0%,0%,.15);
    color: white;
  }

  .widget-extension__button:hover {
    background-color: #333333;
  }

  .widget-extension__button p {
    font-weight:600;
    font-size:16px;
    user-select: none;
    margin-right:6px;
  }

  .widget-extension__button:hover p {
    color:white;
  }

  .widget-button-icon,
  .widget-extension__send-icon {
    height: 30px;
  }

  .qh_module--active-operator {
    height:100%;
  }

  .qh_video-ui__body {
    padding:0 10px;
  }

  .qh_module {
    width:100%;
  }

  .qh_module,
  .video-ui__stage,
  .qh_module--active-operator {
    display: none;
  }

  .qh_module.js-enabled,
  .video-ui__stage.js-enabled,
  .qh_module--active-operator.js-enabled {
    display: flex;
  }

  .qh_module--inactive-form.js-enabled {
    display:block;
  }

  .form-complete__logo-icon {
    position: absolute;
    bottom:19px;
    right:10px;
  }

  p.widget__form-label {
    font-weight: 500;
    line-height:16px;
    pointer-events: none;
  }
  p.widget__form-sublabel {
    font-weight: 400;
    pointer-events: none;
  }
  
  .inactive-user__form-label {
    margin-bottom:4px;
    font-size:16px;
  }
  
  .inactive-user__form-sublabel {
    font-size:1.375rem;
    margin-bottom: 20px;
  }

  input.widget-extension__input {
    width:100%;
  }

  .qh-widget-ext__contact-form-body {
    height:100%;
  }

  .el-group {
    margin-bottom:20px;
  }

  .active-form__subtext {
    line-height:1.1em;
    font-weight:300;
  }

  .active-form__subtext span {
    font-weight:600;
  }

  .widget-outlook__admin-thumbnail {
    width:30px;
    height:30px;
    top:0;
    left:0;
    position: absolute;
    z-index:4;
  }

  .avatar {
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 50%;
    background-position: center;
  }

  .active-user-form__thumbnail {
    width:60px;
    height:60px;
  }

  .outlook__circle-blue {
    position:absolute;
    top:-6px;
    left:-6px;
  }

  .outlook__user-icon {
    position:absolute;
    top:4px;
    left:5px;
    width:20px;
    height:20px;
  }

  .widget-outlook__background-circle.not-active {
    fill:#878787;
  }

  .widget-outlook__background-circle.js-active {
    fill:#7fd100;
  }

  .widget-bottom__msg {
    color:#08005a;
    font-weight: 300;
  }

  .widget-bottom__msg b {
    font-weight: 500;
  }

  .widget-bottom__error {
    color:#c90000;
  }

  div.qh_widget-bottom__content.js-hidden,
  p.widget-bottom__error.js-hidden {
    display: none;
  }
  
  .widget-bottom__txt {
    font-size:14px;
    text-align: right;
    width:100%;
  }

  .widget-bottom__msgs {
    margin-right:20px;
  }

  .widget-bottom__button p {
    font-size:1.275rem;
    color:#08005a;
    pointer-events: none;
    font-weight: 300;
  }
  .widget-bottom__button:hover p {
    color:white;
    @include transition(color .1s ease);
  }
  .widget-bottom__button {
    padding:1px;
    border-radius: 12px;
    border-style: solid;
    border-width: 2px;
    padding:4px 10px;
    cursor:pointer;
  }

  .widget-bottom__button--mute {
    height:44px;
    margin-right:4px;
    padding-bottom:4px;
    cursor:pointer;
  }

  .widget-bottom__button:hover {
    background-color: #08005a;
    border-color: #08005a;
    @include transition(background-color .1s ease);
  }

  .widget-bottom__button--yes {
    border-color:#7fd100;
    margin-right:10px;
  }
  .widget-bottom__button--no {
    border-color:#cd0a0a;
  }

  .qh-widget-ext__contact-form,
  .qh_module--inactive-form {
    height:100%;
  }

  .qh-widget-ext__content {
    height:100%;
  }

  .qh_video-ui_form {
    height:100%;
  }

  .qh_tight {
    padding:0 10px;
  }

  .qh_video-ui_awating-texts {
    margin-bottom:40px;
  }

  .qh_video-ui_awating p {
    font-size:16px;
    font-weight:300;
  }

  .qh_video-ui_awating--thankyou {
    font-weight:500;
  }

  .qh_submodule {
    display:none;
  }
  .qh_submodule.js-enabled {
    display:block;
  }
  .qh_submodule--video-chat {
    width:100%;
    height:100%;
    padding:0 6px 2px;
    transform: translateY(-4px);
  }

  div.main.qh_video,
  div.view--videos {
    height:100%;
  }

  div.qh_submodule--rate-ux,
  div.qh_rate-ux-content {
    height:100%;
  }

  .qh_rate-ux--top {
    margin-bottom:100px;
  }

  p.qh_text--chat-ended {
    font-weight: 400;
  }
  p.qh_text--thank-you {
    font-size: 3rem;
    font-weight:600;
  }
  
  p.qh_rate-desc {
    font-size:.875em;
    font-weight: 300;
  }
}

.qh_rate-star {
  cursor: pointer;
}

.qh_rate-star svg {
  width:36px;
  height:auto;
}

.qh_rate-star g.qh_rate-image--disabled,
.qh_rate-star.js-disabled g.qh_rate-image--enabled {
  display: none;
}

.qh_rate-star.js-disabled g.qh_rate-image--disabled,
.qh_rate-star g.qh_rate-image--enabled {
  display: block;
}

.st0 {
  fill: none;
  stroke: #ffffff;
  stroke-width: 1.5;
}
.st1 {
  fill: #ffffff;
}

div.window {
  // width: 100%;
  // height: 100%;
}

div.widget--schedule {
  background: transparent;
  border: none;
  bottom: 1px;
  display: none;
  opacity: 0;
  right: -4px;
  top: auto;
  transition: bottom .2s ease-in;
  z-index: 1003;
  overflow-x: hidden;
}

.widget__content {
  position: relative;
  padding: 0;
  border-radius: 15px;
  height:100%;
  overflow: hidden;
  border: 4px solid rgba(0, 0, 0, 0.15);
  background-color: #7fd100;
}

.widget--schedule__datetime {
  display: none;
}

div.js-expanded,
#qh_e_frame.js-expanded {
  opacity:1;
  display: block;
}

#qh_e_frame {
  background: transparent;
  border: none;
  z-index: 1002;
  transition: bottom 0.2s ease-in;
  box-shadow: 0 0 5px 0 #ddd;
  opacity:0;
  border-radius: 12px;
  display: none;
}

.widget--schedule, 
.qh-widget-ext {
  position: fixed;
  width: calc(100vw - 8px);
  height: calc(100vh - 8px);
  top: 4px;
  left:4px;
}

#qh_e_frame.qh_video-mode {
  width:90vw;
  height:90vh;
}

#qh_b_frame {
  background: white;
  border: none;
  box-shadow: 0px -1px 5px 0px #ddd;
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 1001;
  padding:4px 20px;
}

.widget-bottom {
  @include transform(translateY(110%));
  @include transition(transform .2s ease);
}

.widget-bottom.js-active {
  @include transform(translateY(0%));
}

.qh-widget-badge {
  @include transition(transform .2s ease);
}

.qh-widget-badge {
  bottom: 8px;
  right: 8px;
  position:fixed;
}

.qh-widget-badge.js-top {
  @include transform(translateY(-56px));
}

.margin-box {
  margin-bottom:20px;
}

#message-input {
  margin-bottom:10px;
}

div.qh-root.active {
  opacity:1!important;
  transition: opacity .3s ease-out;
}

/// videochat

.column--video {
  position: relative;
  height: 100%;
  background-color: black;
  overflow: hidden;
}

.videos-wrap,
.video-columns-wrap {
  max-height: calc(100% - 40px);
  @media (min-width: $s-md) {
    max-height:calc(100% - 75px);
  }
}

.videos-wrap {
  @include transition(all .2s ease);
  width: 100%;
  height: 100%;
  position: absolute;
  max-height: calc(100% - 40px);
  @media (min-width: $s-md) {
    max-height:calc(100% - 75px);
  }
}

.video-columns-wrap.js-fullscreen,
.videos-wrap.js-fullscreen {
  max-height: 100%;
}

.remote-video-fullscreen,
div.fullscreen {
  display:none!important;
}

.qh_video-columns {
  height:100%;
}

div.fullscreen,
.qh_video-control {
  background-image: none !important;
}

.qh_video-control svg {
  height:32px;
}

.videos-wrap.active {
  @include transform(rotateY(0));
}

.rejoin-button {
  background-color: #f44336;
}

.new-room-button,
.rejoin-button {
  font-size: 0.75rem;
  font-weight: 400;
  border: none;
  border-radius: 20px;
  padding: 5px 10px;
  white-space: nowrap;
  @media (min-width: $s-sm) {
    font-size:1.25rem;
    padding: 5px 20px;
  }
}

.qh_room-link,
.rejoin-button,
.new-room-button,
.button--send-invitation {
  display: none!important;
}

.rejoin-button {
  margin-right: 10px;
}

.text-left-call {
  color: white;
  display: none;
}

.rejoin-div {
  margin-right: 20px;
}

.local-video {
  height: 100%;
  max-height: 100%;
  max-width: 100%;
  object-fit: cover;
  transition: opacity 1s;
  width: 100%;
}

.mini-video {
  border: 1px solid gray;
  top: 20px;
  left: 20px;
  max-height: 17%;
  max-width: 17%;
  opacity: 0;
  position: absolute;
  transition: opacity 1s;
}

.mini-video.active {
  opacity: 1;
  z-index: 2;
}

.widget--schedule__datetime-controls {
  @include df;
  width: calc(100% - 40px);
  margin-left: 20px;
  margin-right: 20px;
  height:calc(100% - 200px);
  overflow: hidden;
  margin-bottom: 10px;
}

div.scheduler__page {
  min-width: 100%;
  @include df;
  @include df-col;
  overflow-y: auto;
  height: calc(100% - 30px);
}

div.scheduler__days {
  min-width: 100%;
  @include df;
  @include df-col;
  @media (min-width: $s-sm) {
    overflow-y: auto;
    height: 100%;
    @include df-lt;
    @include df-row;
  }
}

div.scheduler__day {
  font-weight: 600;
  color: white;
  border-radius: 10px;
  padding: 5px;
  margin: 3px;
  border: 1px solid rgba(0,0,0,0.25);;
  @media (min-width: $s-sm) {
    padding: 5px;
    width: 15%;
    border: none;
  }
}

div.scheduler__day:hover {
  background-color: rgba(255,255,255,0.25);
}

.scheduler__day.today {
  box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.25);
}

.scheduler,
.scheduler__pages {
  width: 100%;
  max-width: 100%;
  height: 100%;
}

div.scheduler__pages {
  transition: transform .4s ease-out;
  
  @include df-col;
  @include df-lt;
  overflow-y: auto;
  @media (min-width: $s-sm) {
    overflow-y: unset;
    @include df-row;
  }
}

.scheduler__page-buttons {
  height: calc(100vh - 160px);
  overflow-y: auto;
  align-items: flex-start;
}

.scheduler__pages-nav-button {
  cursor:pointer;
  user-select: none;
  background-color: rgba(0,0,0,.6);
  box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.25);
  border-radius: 50%;
  padding: 6px;
  color: white;
  width: 30px;
  min-width: 30px;
  height: 30px;
  transition: background-color .2s linear, border-color .2s linear;
  display: none;
  @media (min-width: $s-sm) {
    @include df;
  }
}

.scheduler__pages-nav-button:hover {
  background-color: rgba(0,0,0,1);
  box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.55);
}

.scheduler__pages-nav-button--prev {

}

.scheduler__pages-nav-button--next {

}


.outlook .qh-root {
  position: relative;
  bottom: unset;
  right: unset;
  z-index: 0;
}


.scheduler__time {
  color: white;
}

.scheduler__button {
  border-radius: 6px;
  padding:7px;
  color: white;
  background-color: rgba(0, 0, 0, 0.2);
  transition: background-color .2s linear;
  cursor: pointer;
}

.scheduler__button:hover {
  background-color: rgba(0, 0, 0, 0.6);
  box-shadow: 0 0 0 2px rgba(0, 0, 0, 0.15);
}

.scheduler__page-table {
  min-width: 100%;
}

.scheduler__day-text {
  font-size: 12px;
  text-align: center;
  font-weight: 300;
  color: #1b1b1b;
}

.scheduler__day-name {
  font-size: 20px;
  font-weight: 600;
  color: #1b1b1b;
  padding: 5px 0;

  @media (min-width: $s-sm) {
    font-size: 12px;
  }  
}

.scheduler__time-buttons {
  height:300px;
  overflow-y: auto;
}

.scheduler__button--time {
  margin-bottom: 6px;
}

.scheduler__button.js-enabled {
  background-color: #1b1b1b;
  box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.5);
}

.scheduler__button.js-enabled .scheduler__button-text {
  color: white;
}

.scheduler__button-text {
  font-size: 11px;
  pointer-events: none;
  user-select: none;
  color: white;
  white-space: nowrap;
}

.scheduler__day-header_wrap {
  margin-top: 10px;
  margin-bottom: 10px;

  @media (min-width: $s-sm) {
    margin-top: 0px;
  }    
}

.scheduler__page-headers {
  padding-right: 17px;
}

.widget-extension__controls {
  width: 100%;
  padding:10px 30px;
}

.scheduler__day-info {
  font-size: 12px;
  color: #1b1b1b;
  font-weight: 300;

  @media (min-width: $s-sm) {
    display: none !important;
  }  
}

.qh-widget-ext__header {
  background-color: #1b1b1b;
  color: white;
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 20px;
}

.widget__header-title {
  color: white;
  font-weight: 600;
  font-size: 18px;
}

.widget__header-subtitle {
  color: white;
  font-weight:300;
  font-size: 18px;
}

.qh-widget__form {
  padding: 20px 30px 0px;
  flex-direction: column-reverse;

  @media (min-width: $s-sm) {
    flex-direction: row;
  }    

}

.qh_footer {
  padding: 8px;
  width: 100%;
}

.qhellou-footer_logo {
  width: 30px;
}

.qhellou-footer_label {
  font-size: 11px;
}

.widget--schedule__scheduler {
  position: relative;
  margin-top: 30px;

  @media (min-width: $s-sm) {
    margin-top: unset;
  }    

}

.scheduler__paging_row {
  width: 100%;
  padding: 10px;
  padding-top: 20px;  
  display: none !important;
  z-index: 1;

  @media (min-width: $s-sm) {
    display: flex !important;
    margin-bottom: -50px;
  }     
}

.scheduler__date-label {
  width: 100%;
  margin-bottom: 20px;
}

.scheduler__month-label {
  font-weight: 600;
  margin-right: 5px !important;
}

.qh-widget__form--inputs {
  width: 100%;
}

.qh-widget__form--calendar-selected {
  padding: 20px;
  margin-left: 0px;
  width: 100%;

  @media (min-width: $s-sm) {
    margin-left: 30px;
    width: 200px;
    min-width: 200px;
  }     
}

.qh-widget__form--date-selected {
  padding: 20px;  
  width: 100%;
  height: 100%;
  border-radius: 20px;
  background-color: #1b1b1b;
  box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.25);
  color: white;
}

.qh-widget__form--time-selected {
  font-size: 20px;
  font-weight: 300;
}

.qh-widget__form--day-selected {
  font-size: 40px;
  font-weight: 600;
}

.qh-widget__form--month-selected {
  font-size: 24px;
  font-weight: 600;
}

.qh-widget__form--year-selected {
  font-size: 14px;
  font-weight: 300;
}

.qh-widget__change-date-time_link {
  margin-top: 10px;
  text-decoration: underline;
  font-size: 14px;
}

.qh-widget__change-date-time_link:hover {
  text-decoration: none;
  opacity: 0.6;
}

.qh-root.failure {
  background-color: #c90000;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  overflow: hidden;
  top: 0;
  left: 0;
  color: white;
  text-align: center;
  font-size: 9px;
  font-weight: 600;
  padding: 12px;
  align-items: center;
  display: flex;
  border: solid 3px rgba(255, 255, 255, 0.25);  
}